body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.playArea
{
  width: 100%;
  height:100dvh;
  overflow: hidden;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(rgb(156, 92, 106),grey,darkslategrey);
}

.enemyCards,.yourCards
{
  height:20dvh;
  width:100%;
  display:flex;
  overflow: auto;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.battleColumns
{
  height:60dvh;
  width:100%;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}
.battleColumn
{
  height:60dvh;
  width:22.5dvh;
  overflow: hidden;
  display:flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-image: url("../public/cardImages/battleColumnPlaceholder.png");
  background-size:cover;
}
.enemyBattleColumn,.yourBattleColumn
{
  height:25dvh;
  width:22.5dvh;
  overflow: hidden;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  div
  {
    height: 12vmin;
  }
}
.cardUnknown,.cardVisible
{
  margin:1vmin;
  padding:1vmin;
  height:12vmin;
  width:20vmin;
  border-radius: 3vmin;
  display:flex;
  align-items: center;
  justify-content: center;
  color:white;
  text-align: center;
}
.cardVisible
{
  background-image: url("../public/cardImages/background.png");
  background-size: cover;
  img
  {
    width: inherit;
    height:inherit;
    object-fit: contain;
  }
}
.cardUnknown
{
  background-image: url("../public/cardImages/unknown.png");
  background-size: cover;
}
.chosen
{
  border:2px solid rgba(255,255,255,0.8);
}
.available{
  border: 2px solid rgba(0,255,0,0.8);
}
.unavaiable
{
  border: 2px solid rgba(255,0,0,0.8);
}

